const API_BASE_URL = 'http://127.0.0.1:8002/api';

export interface Todo {
    id: number;
    title: string;
    description?: string;
    completed: boolean;
    created_at: string;
    updated_at: string;
}

export interface CreateTodoRequest {
    title: string;
    description?: string;
    completed?: boolean;
}

export interface UpdateTodoRequest {
    title?: string;
    description?: string;
    completed?: boolean;
}

class ApiService {
    private async request<T>(
        endpoint: string,
        options: RequestInit = {}
    ): Promise<T> {
        const url = `${API_BASE_URL}${endpoint}`;
        
        const config: RequestInit = {
            headers: {
                'Content-Type': 'application/json',
                ...options.headers,
            },
            ...options,
        };

        try {
            const response = await fetch(url, config);
            
            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(`API Error: ${response.status} - ${errorText}`);
            }
            
            // –î–ª—è DELETE –∑–∞–ø—Ä–æ—Å–æ–≤ —Å 204 —Å—Ç–∞—Ç—É—Å–æ–º –≤–æ–∑–≤—Ä–∞—â–∞–µ–º null
            if (response.status === 204) {
                return null as T;
            }
            
            return await response.json();
        } catch (error) {
            console.error('API Request failed:', error);
            throw error;
        }
    }

    // –ü–æ–ª—É—á–∏—Ç—å –≤—Å–µ –∑–∞–¥–∞—á–∏
    async getTodos(sphereId?: number): Promise<Todo[]> {
        try {
            const todos = await this.request<Todo[]>('/todos');
            
            // –ï—Å–ª–∏ —É–∫–∞–∑–∞–Ω sphereId, —Ñ–∏–ª—å—Ç—Ä—É–µ–º –∑–∞–¥–∞—á–∏ –ø–æ —Å—Ñ–µ—Ä–µ
            if (sphereId) {
                // –í —Ä–µ–∞–ª—å–Ω–æ–º –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–∏ –∑–¥–µ—Å—å –±—ã–ª–∞ –±—ã —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏—è –ø–æ sphereId
                // –ü–æ–∫–∞ –≤–æ–∑–≤—Ä–∞—â–∞–µ–º –≤—Å–µ –∑–∞–¥–∞—á–∏ –¥–ª—è –¥–µ–º–æ–Ω—Å—Ç—Ä–∞—Ü–∏–∏
                return todos;
            }
            
            return todos;
        } catch (error) {
            console.error('Failed to fetch todos:', error);
            // –í —Å–ª—É—á–∞–µ –æ—à–∏–±–∫–∏ API –≤–æ–∑–≤—Ä–∞—â–∞–µ–º –ø—É—Å—Ç–æ–π –º–∞—Å—Å–∏–≤
            return [];
        }
    }

    // –°–æ–∑–¥–∞—Ç—å –∑–∞–¥–∞—á—É
    async createTodo(todo: CreateTodoRequest): Promise<Todo> {
        return this.request<Todo>('/todos', {
            method: 'POST',
            body: JSON.stringify(todo),
        });
    }

    // –û–±–Ω–æ–≤–∏—Ç—å –∑–∞–¥–∞—á—É
    async updateTodo(id: number, updates: UpdateTodoRequest): Promise<Todo> {
        return this.request<Todo>(`/todos/${id}`, {
            method: 'PUT',
            body: JSON.stringify(updates),
        });
    }

    // –£–¥–∞–ª–∏—Ç—å –∑–∞–¥–∞—á—É
    async deleteTodo(id: number): Promise<void> {
        return this.request<void>(`/todos/${id}`, {
            method: 'DELETE',
        });
    }

    // –ü–æ–ª—É—á–∏—Ç—å –æ–¥–Ω—É –∑–∞–¥–∞—á—É
    async getTodo(id: number): Promise<Todo> {
        return this.request<Todo>(`/todos/${id}`);
    }
}

export const apiService = new ApiService();

// –£—Ç–∏–ª–∏—Ç—ã –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å localStorage –∫–∞–∫ fallback
export const localStorageService = {
    getTodos: (sphereId: number): any[] => {
        if (typeof window === 'undefined') return [];
        const tasks = localStorage.getItem(`sphere_${sphereId}_tasks`);
        if (!tasks) return [];
        try {
            return JSON.parse(tasks);
        } catch {
            return [];
        }
    },

    saveTodos: (sphereId: number, todos: any[]): void => {
        if (typeof window === 'undefined') return;
        localStorage.setItem(`sphere_${sphereId}_tasks`, JSON.stringify(todos));
        // –¢—Ä–∏–≥–≥–µ—Ä –¥–ª—è –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –¥—Ä—É–≥–∏—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤
        window.dispatchEvent(new Event('taskUpdated'));
    },

    // –ö–æ–Ω–≤–µ—Ä—Ç–∞—Ü–∏—è –º–µ–∂–¥—É —Ñ–æ—Ä–º–∞—Ç–∞–º–∏ API –∏ localStorage
    apiToLocalStorage: (apiTodos: Todo[], sphereId: number): any[] => {
        return apiTodos.map(todo => ({
            id: todo.id.toString(),
            description: todo.title,
            isCompleted: todo.completed,
            subtasks: todo.description ? [{
                id: `sub-${todo.id}`,
                description: todo.description,
                isCompleted: todo.completed
            }] : []
        }));
    },

    localStorageToApi: (localTodos: any[]): CreateTodoRequest[] => {
        return localTodos.map(todo => ({
            title: todo.description,
            description: todo.subtasks?.[0]?.description || '',
            completed: todo.isCompleted
        }));
    }
};
